.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.bg-1 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 150%;
		opacity: 0.2;
		transform: translateX(-50%);
	}
	.orn-1 {
		position: absolute;
		top: 0;
		right: 0;
		width: 50%;
		transform: translate(85%, -20%) scaleX(-1);
		@include min-md {
			width: 35%;
		}
		@include min-lg {
			width: 25%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 120px;
		transform: translate(60%, 0) scaleX(-1);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 140px;
		transform: translate(-60%, 15%) rotate(10deg);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/5;
		}
		@include max-sm {
			aspect-ratio: 5/6;
		}
		.image-element {
			height: 100%;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				clip-path: polygon(0 0, 100% 15%, 100% 100%, 0 85%);
			}
		}
	}
	.stream-wrapper {
		position: relative;
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				padding: 2rem 1rem;
				overflow: hidden;
				border-radius: var(--border-radius);
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		position: relative;
	}
}
